<template>
	<div>
		<!-- 头部定位 -->
		<header>
			<div class="location">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="icon-location">
					<path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z"></path>
				</svg>
				<span>保定东站</span>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="icon-arrow">
					<path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z">
					</path>
				</svg>
			</div>
			<elm-tabbar></elm-tabbar>
		</header>
		<!-- 搜索框 -->
		<section class="search">
			<div class="button">
				<a href="">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16">
						<path fill-opacity=".38" d="M14.778 13.732a.739.739 0 1 1-1.056 1.036l-2.515-2.565a.864.864 0 0 1-.01-1.206 4.894 4.894 0 0 0 1.357-3.651c-.126-2.492-2.156-4.52-4.648-4.647a4.911 4.911 0 0 0-5.16 5.163c.126 2.475 2.13 4.496 4.605 4.642.451.026.896-.008 1.326-.1a.739.739 0 0 1 .308 1.446c-.56.12-1.137.164-1.72.13-3.227-.19-5.83-2.815-5.995-6.042a6.39 6.39 0 0 1 6.71-6.715c3.25.165 5.884 2.8 6.05 6.048a6.37 6.37 0 0 1-1.374 4.3l2.12 2.161z">
							
						</path>
					</svg>
					<span>搜索饿了么商家、商品名称</span>
				</a>
			</div>
		</section>
		<!-- 热门搜索 -->
		<section class="hot-search">
			<ul>
				<li v-for="(item,index) in hotsearches" :key="index">{{item.word}}</li>
			</ul>			
		</section>
		<!-- 食品分类轮播图 -->
		<section class="entries">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item,index) in entries" :key="index">
					<a href="" v-for="(item2,index2) in item" :key="index2">
						<img :src="item2.image_hash|imgUrl" alt="">
						<span>{{item2.name}}</span>
					</a>
				</mt-swipe-item>
			</mt-swipe>
		</section>
		<!-- 推荐商家 -->
		<section class="recommends">
			<!-- 标题 -->
			<section class="shop-title">
				<h3>推荐商家</h3>
			</section>
			<section class="shop-list">
				<div class="shop-info" v-for="(item,index) in restaurants" :key="index">
					<!-- logo部分 -->
					<div class="shop-logo">
						<img :src="item.restaurant.image_path|imgUrl" alt="">
					</div>
					<!-- 右边主体部分 -->
					<div class="shop-main">
						<!-- 商家名称 -->
						<div class="title">
							<h3>{{item.restaurant.name}}</h3>
							<ul>
								<li>广告</li>
								<li>保</li>
							</ul>
						</div>

						<!-- 商家评分 -->
						<div class="rating">
							<div class="ratingWrap">
								<elm-star :score="item.restaurant.rating" :size="24"></elm-star>
								<span>{{item.restaurant.rating}}</span>
								<span>月销{{item.restaurant.recent_order_num}}单</span>
							</div>
							<!-- 配送方式 -->
							<div class="deliver-mode">
								<span>蜂鸟专送</span>
							</div>
						</div>

						<!-- 配送信息 -->
						<div class="deliver">
							<div class="money">
								<span>￥{{item.restaurant.float_minimum_order_amount}}起送</span>
								<span>配送费￥{{item.restaurant.float_delivery_fee}}</span>
							</div>
							<div class="distance">
								<span>{{item.restaurant.distance/1000 | Mydecimal}}km</span>
								<span>{{item.restaurant.order_lead_time}}分钟</span>
							</div>
						</div>

						<!-- 商家活动 -->
						<div class="shop-activity">
							<div class="activities">
								<div class="activity-list" v-for="(item,index) in item.restaurant.activities" :key="index">
									<span style="background-color: rgb(112, 188, 70);">{{item.icon_name}}</span>
									<span >{{item.tips}}</span>
								</div>
							</div>
							<div class="activity-btn">
								<span>{{item.restaurant.activities.length}}个活动</span>
								<img src="data:image/svg+xml;base64," class="">
							</div>
						</div>
					</div>
				</div>
			</section>
		</section>
		<elm-tabbar></elm-tabbar>
	</div>
</template>
<script type="text/javascript">
	import Tabbar from '@/components/footer/Tabbar'
	import {getHotSearches,getentries,getRestaurants} from "@/api/msite"
	import _ from "lodash"

	import { Swipe, SwipeItem} from 'mint-ui';

	import "mint-ui/lib/style.css"
	import "mint-ui/lib/index.js"

	import Vue from "vue"
	Vue.component(Swipe.name,Swipe);
	Vue.component(SwipeItem.name,SwipeItem);

	import Star from "@/components/star/Star"

	export default{
		components:{
			//给Tabbar组件取别名，elm-tabbar
			'elm-tabbar':Tabbar,
			'elm-star':Star
		},
		data(){
			return{
				hotsearches:[],
				entries:[],
				restaurants:[]
			}
		},
		created(){
			getHotSearches()
			.then((res) => {
				//console.log(res.data)
				this.hotsearches = res.data;
			})
			.catch((err) => {
				console.log(err)
			})
			//获取分类轮播图的数据
			getentries()
			.then((data) =>{
				//console.log(data.data[0].entries)
				this.entries = _.chunk(data.data[0].entries,8);
				// console.log(this.entries)
			})
			.catch((err) =>{
				console.log(err);
			})

			//获取商家列表
			getRestaurants()
			.then((res) =>{
				console.log(res.data.items)
				this.restaurants = res.data.items;
			})
			.catch((err) =>{
				console.log(err)
			})
		},
		//过滤器
		// https://fuss10.elemecdn.com/a/78/0fb469b2da210827ec16896e00420jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/
		filters:{
			imgUrl(src){
				let url = "https://fuss10.elemecdn.com/";
				url += src.slice(0,1)+"/"+src.slice(1,3)+"/"+src.slice(3);
				let reg = /jpeg|jpg|png$/;
				let result = url.match(reg);
				url += "."+result[0]+"?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/";
				return url;
			},
			Mydecimal(data){
				return data.toFixed(2);
			}
		}
	}
</script>
<style lang="less" scoped>
	*{box-sizing:border-box}
	header{
		padding: .266667rem .373333rem 0;
		background-image: linear-gradient(90deg,#0af,#0085ff);
   		color: #fff;
   		height:1.333333rem;
   		display:flex;
   		align-items:center;
   		.location{
   			display:flex;
   			align-items:center;
   			span{
			    font-size: .453333rem;
			    max-width: 80%;
			    white-space: nowrap;
			    overflow: hidden;
			    text-overflow: ellipsis;
   			}
   			.icon-location{
   				width:0.4rem;
   				height:0.426667rem;
   			}
   			.icon-arrow{
   				width:0.186667rem;
   				height:0.106667rem;
   			}
   		}
	}
	.search{
		width:100%;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		padding:0.5rem 0.346667rem 0.346667rem;
		display:flex;
		align-items:center;
		justify-content:center;
		position: sticky;
		top: 0;
		z-index: 999;
		.button{
			width:100%;
			height:0.986667rem;
			line-height:0.986667rem;
			background:#fff;
			a{
				display:flex;
				align-items:center;
				justify-content:center;
				text-decoration:none;
				svg{
					width:0.386667rem;
					height:0.386667rem;
				}
				span{
					font-size:0.373333rem;
					color:#999;
					margin-left:0.16rem;
				}
			}
		}
	}
	.hot-search{
		background-image: linear-gradient(90deg,#0af,#0085ff);
		height:1.12rem;
		ul{
			display:flex;
			align-items:center;
			white-space:nowrap; 
			overflow-x:auto;
			&::-webkit-scrollbar{
				display:none;
			}
			li{
				font-size:0.32rem;
				display:inline-block;
				color:#fff;
				&:not(:last-child){
					margin-right:0.48rem;
				}
			}
		}
	}
	.entries{
		height:4.26667rem;
		a{
			text-decoration:none;
			display:flex;
			flex-direction:column;
			align-items:center;
			float:left;
			width:25%;
			margin-top:0.533333rem;
			img{
				width:0.933333rem;
				heigth:0.933333rem;
			}
			span{
				font-size:0.32rem;
				color:#666;
				margin-top:0.296667rem;
			}
		}
	}
	// 推荐商家
	.recommends{
		.shop-title h3{
			font-size: .426667rem;
			height: 1.333333rem;
			display: flex;
			justify-content: center;
			align-items: center;
			&:before,&:after{
				content: "";
				display: block;
				width: 0.533333rem;
				height: 0.026667rem;
				background-color: #999;
			}
			&:before{
				margin-right: 0.346667rem;
			}
			&:after{
				margin-left: 0.346667rem;
			}
		}
		//商家列表
		.shop-info{
			display: flex;
			justify-content: flex-start;
			align-items: stretch;
			padding: 4vw 0.266667rem;
			// padding: 4vw 0;
			.shop-logo{
				width: 1.733333rem;
				height: 1.733333rem;
				img{
					width: 100%;
					height: 100%;
				}
			}
			
			.shop-main{
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				padding: 0 0.266667rem;
				flex-grow: 1;
				//商家名称
				.title{
					display: flex;
					justify-content: space-between;
					h3{
						font-size: 0.4rem;
						color: #333;
						font-weight: 700;
					}
					ul{
						display: flex;
						align-items: center;
						li{
							font-size: 0.266667rem;
						}
					}
				}
				//商家评分
				.rating{
					display: flex;
					justify-content: space-between;
					span{
						font-size: 0.293333rem;
					}
					.deliver-mode span{
						background-image: linear-gradient(45deg,#0085ff,#0af);
						color: #fff;
					}
				}
				//配送信息
				.deliver{
					display: flex;
					justify-content: space-between;
					span{
						font-size: 0.293333rem;
					}
				}
				//商家活动
				.shop-activity{
					display: flex;
					justify-content: space-between;
					.activities{
						display: flex;
						flex-direction: column;
						white-space: nowrap;
					}
					span{
						font-size: .293333rem;
					}
				}
			}
		}
	}
	

</style>


